.d-flex {
    display: flex;
}
.movies-list {
    width: 15%;
}
.movies-list__movie {
    margin: 10px 0;
    padding: 20px;
    border: 0.5px solid black;
    border-radius: 5px;
}
.movies-list__movie p {
    margin: 0 0 10px 0;
}
.movies-list__movie h3 {
    margin: 0;
}
.movie {
    margin-left: 10px;
    display: flex;
}
.movie-img {
    float: left;
    width:  150px;
    height: 200px;
    object-fit: cover;
}
.movie-info {
    margin-left: 10px;
    max-width: 400px;
}
.movie-info__rating {
    margin: 0;
}
.movie-info__release {
    margin: 0;
}
.movie-info__overview {
    margin-top: 10px;
}